import React, { Component } from 'react'
import NApi from '../../Component/NumbersAPI/NApi'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs'
import SecondAPI from '../../Component/SecondAPI/SecondAPI'
import BondMain from '../Home/images/Bond/1.png'
import BondF1 from '../Home/images/Bond/Prevailing Interest Rates.svg'
import BondF2 from '../Home/images/Bond/Age of the Bond.svg'
import BondF3 from '../Home/images/Bond/Credit Quality.svg'
import BondCalc from '../../Component/BondCalculator/BondCalculator'
import {withTranslation} from 'react-i18next';
import CB1 from '../Home/images/Corporate bond/1.png';
import NCDMain from '../Home/images/NCD/1.png'
import RBIMain from '../Home/images/SGB/1.png'
import SGBMain from '../Home/images/SGB/1.png'
import {Helmet} from 'react-helmet';

export class CB extends Component {
   constructor() {
      super()
   }

   data = {
      "Bond": {
         "Title": "",
         "ProductCategoryId": "7",
         "SebiCategory": "",
         "SebiSubCategory": "",
         "DefaultProductId": "1",
      }
   }

   render() {
      const productinfo = this.data.Bond;
      const { t } = this.props;

      return (
         <main>
            <Helmet>
                <meta charSet="utf-8" />
                <title>{t('MetaTitle:mT45')}</title>
                <meta name="description" content={t('MetaDescription:mD45')} />
                <link rel="canonical" href={window.location.href} />
            </Helmet>
            <div className="container container-wrapper">
               <div className="row">
                  <div className="col-lg-7 col-md-6 mb-4">
                     <h2>{t('Bond:bondPF1')}</h2>
                     <p>{t('Bond:bondPF2')}</p>
                     <div className="registerFair">
                        <a href="https://app.nivesh.com/home/index"><button type="button" className="btn btn-primary btn-lg">{t('CTAFooter:InvestNow1')}</button></a>
                     </div>
                  </div>
                  <div className="col-lg-5 col-md-6"><img src={BondMain} className='img-fluid' /></div>
               </div>
            </div>

            <div className="API-Section">
               <div className="container container-wrapper">
                  <h2 className='text-center'>{t('Bond:bondPF3')}</h2>
                  <div className='row row justify-content-lg-center'>
                     <div className='col col-lg-10'>
                        <SecondAPI productCategoryId={productinfo.ProductCategoryId} sebiCategory={productinfo.SebiCategory} sebiSubCategory={productinfo.SebiSubCategory} defaultProductId={productinfo.DefaultProductId} />
                     </div>
                  </div>
               </div>
            </div>

            <div className="container features">
               <div className="row row justify-content-center">
                  <div className="col-lg-12">
                     <h2>{t('Bond:bondPF11')}</h2>

                  </div>
                  <div className="col-lg-4 col-md-6">
                     <div className="imgFeatures">
                        <img src={BondF1} />
                        <h4>{t('Bond:bondPF12')}</h4>
                        <p className='small-text'>{t('Bond:bondPF13')}</p>
                     </div>
                  </div>
                  <div className="col-lg-4 col-md-6">
                     <div className="imgFeatures">
                        <img src={BondF2} />
                        <h4>{t('Bond:bondPF14')}</h4>
                        <p className='small-text'>{t('Bond:bondPF15')}
                        </p>
                     </div>
                  </div>
                  <div className="col-lg-4 col-md-6">
                     <div className="imgFeatures">
                        <img src={BondF3} />
                        <h4>{t('Bond:bondPF16')}</h4>
                        <p className='small-text'>{t('Bond:bondPF17')}
                        </p>
                     </div>
                  </div>
               </div>
               <div className="text-center"><a href="https://app.nivesh.com/home/index"><button type="button" className="btn btn-primary btn-lg">{t('CTAFooter:InvestNow1')}</button></a></div>
            </div>

            <div className='container'>

               <h2>{t('Bond:bondPF5')}</h2>
               <p>{t('Bond:bondPF6')}</p>
               <ul>
                  <li>{t('Bond:bondPF7')}</li>
                  <li>{t('Bond:bondPF8')}</li>
               </ul>
               <p>{t('Bond:bondPF9')}<br/>{t('Bond:bondPF10')}</p>

               <h2>{t('Bond:bondPF18')}</h2>
               <p>{t('Bond:bondPF19')}
               </p>

            </div>

            <div className="container">
               <h2>{t('Bond:bondPF20')}</h2>

               <Tabs>
                  <TabList>
                     <Tab>{t('Bond:bondPF21')}</Tab>
                     <Tab> {t('Bond:bondPF27')}</Tab>
                     <Tab>{t('Bond:bondPF29')}</Tab>
                     <Tab>{t('Bond:bondPF25')}</Tab>

                  </TabList>
                  <TabPanel>
                     <p>
                     {t('Bond:bondPF22')}<br/><br/>
                     {t('Bond:bondPF23')}<br/><br/>
                     {t('Bond:bondPF24')}
                     </p>

                  </TabPanel>
                  <TabPanel>
                     <p>{t('Bond:bondPF28')}</p>

                  </TabPanel>
                  <TabPanel>
                     <p>{t('Bond:bondPF30')}
                     </p>
                  </TabPanel>
                  <TabPanel>
                     <p>{t('Bond:bondPF26')}</p>


                  </TabPanel>
               </Tabs>
            </div>

            <NApi />

            <div className='container container-wrapper'>
               <h2 className='text-center'></h2>
               <div className='row row justify-content-center'>
                  <div className='col-lg-3 col-md-6 mt-4'>
                     <div className='card h-100'>
                     <img src={CB1} className='img-fluid' />
                        <div className='card-body'>

                           <h4>Corporate Bond</h4>
                        </div>
                        <div className='card-footer text-center'>
                           <a href="/bond/corporate-bond" className='btn btn-primary'>{t('CTAFooter:MoreButton')}</a>
                        </div>

                     </div>
                  </div>
                  <div className='col-lg-3 col-md-6 mt-4'>
                     <div className='card h-100'>
                     <img src={NCDMain} className='img-fluid' />
                        <div className='card-body'>

                           <h4>Non-Convertible Debentures</h4>
                        </div>
                        <div className='card-footer text-center'>
                           <a href="/bond/non-convertible-debentures" className='btn btn-primary'>{t('CTAFooter:MoreButton')}</a>
                        </div>

                     </div>
                  </div>
                  <div className='col-lg-3 col-md-6 mt-4'>
                     <div className='card h-100'>
                     <img src={RBIMain} className='img-fluid' width="100%" />
                        <div className='card-body'>

                           <h4>RBI Floating Rate Saving Bonds</h4>

                        </div>
                        <div className='card-footer text-center'>
                           <a href="/bond/rbi-floating-rate-saving-bonds" className='btn btn-primary'>{t('CTAFooter:MoreButton')}</a>
                        </div>

                     </div>
                  </div>
                  <div className='col-lg-3 col-md-6 mt-4'>
                     <div className='card h-100'>
                     <img src={SGBMain} className='img-fluid' />
                        <div className='card-body'>

                           <h4>Sovereign Gold Bond</h4>

                        </div>
                        <div className='card-footer text-center'>
                           <a href="/bond/sovereign-gold-bond" className='btn btn-primary'>{t('CTAFooter:MoreButton')}</a>
                        </div>

                     </div>
                  </div>
               </div>
            </div>


            <div className="calCulator">
                    <div className="container container-wrapper">
                        <h2 className='mb-4'> Calculator</h2>
                        <BondCalc />
                    </div>
                </div>

            <div className="container testimonials">
               <div className="row">
                  <div className="col-lg-12">
                     <h2>{t('Bond:bondPF31')}</h2>
                     <div id="accordion">

                        <h3>{t('Bond:bondPF32')}</h3>
                         <p>{t('Bond:bondPF33')}</p>
                         <ul>
                            <li>{t('Bond:bondPF34')}</li>
                            <li>{t('Bond:bondPF35')}</li>
                            <li>{t('Bond:bondPF36')}</li>
                            <li>{t('Bond:bondPF37')}</li>
                         </ul>

                        <h3>{t('Bond:bondPF38')}</h3>
                        <p>{t('Bond:bondPF39')}</p>

                        <h3>{t('Bond:bondPF40')}</h3>
                        <p>{t('Bond:bondPF41')}</p>

                        <h3>{t('Bond:bondPF42')}</h3>
                        <p>{t('Bond:bondPF43')}</p>

                        <h3>{t('Bond:bondPF44')}</h3>
                        <p>{t('Bond:bondPF45')}</p>
                     </div>
                  </div>
               </div>

            </div>
         </main>
      );
   }
}

export default withTranslation() (CB)
